import React from 'react';
import Header from './HeaderComponent';
import Home from './HomeComponent';
import Footer from './FooterComponent';
import Medical from './MedicalComponent';
import DoctorConsultation from './DoctorConsultationComponent'
import {connect} from 'react-redux';
import AboutUs from './AboutUs';
import {addCart} from "../Redux/actionCreat";
import {Cart} from "./cartComponent";
import Product from './HealthProductComponent';
import {Switch, Route, Redirect, withRouter} from 'react-router-dom';

const mapStateToProps = state => {
	return {
		HealthProduct: state.HealthProduct,
		slide: state.slide,
		Lab: state.Lab,
		Medicine: state.Medicine,
		Cart: state.Cart
	}
}

const mapDispatchToProps = dispatch => ({
	addCart: (item) => {dispatch(addCart(item))}
})


class Main extends React.Component{

	render(){
		return(
			<>
			<Header />
			<Switch>
				<Route exact path='/' component={()=> <Home slide={this.props.slide} Lab={this.props.Lab} />} />
				<Route path='/HealthProduct' component={() =>
					<Product item = {this.props.HealthProduct} addCart={this.props.addCart} />
				} />
			  <Route path='/medical' component={()=><Medical medicine={this.props.Medicine} addCart={this.props.addCart}  />}/>
				<Route path='/consultation' component={DoctorConsultation} />
				<Route path='/aboutUs' component={AboutUs} />
				<Route exact path='/cart' component={()=> <Cart item = {this.props.Cart} />} />
				<Redirect to='/' />
			</Switch>
			<Footer />
			</>
			);
	}
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));
