import React from 'react';
import {Card, CardImg, CardBody, Breadcrumb, BreadcrumbItem} from 'reactstrap';
import {Link} from 'react-router-dom';

 export const Cart = (props) => {
  if(props.item.length === 0){
    return (
      <div className="container">
      <div className='row'>
        <Breadcrumb>
          <BreadcrumbItem><Link to ='/'>Home</Link></BreadcrumbItem>
          <BreadcrumbItem active>Cart</BreadcrumbItem>
        </Breadcrumb>
        <div className='col-12'>
          <h4>Your Cart</h4>
          <hr />
        </div>
      </div>
      <div className="container">
        <h4 className="text-danger text-center">Your cart is empty.. <br />
        Please add some item form <Link to="/HealthProduct" >Health Product</Link> or <Link to="/medical">Medical Store</Link> to buy....</h4>
      </div>
      </div>
    );
  }
  else {
  const item= props.item.map((product) => {
     return(

          <div className="col-6 col-sm-4 mt-3"  key={product.id}>
              <Card>
                <CardImg src={product.image} alt={product.name} />
                <CardBody>
                  <h4 className="text-danger">{product.name}</h4>
                  <strong>Price: {product.price}</strong>
                  <h5>Quantity : {product.quantity}</h5>
                  <h6>About Product : <br /> {product.about}</h6>
                  <button className="btn btn-outline-info m-1">Buy Product</button>
                </CardBody>
              </Card>
     
     </div>
   );


  });

  return (
    <div className="container">
    <div className='row'>
      <Breadcrumb>
        <BreadcrumbItem><Link to ='/'>Home</Link></BreadcrumbItem>
        <BreadcrumbItem active>Cart</BreadcrumbItem>
      </Breadcrumb>
      <div className='col-12'>
        <h4>Your Cart</h4>
        <hr />
      </div>
    </div>
      <div className="row">
        {item}
      </div>
    </div>

  );
}
};
